import Vue from 'vue';
import PreviewImgs from './PreviewImgs';
import PhotoSwipe from 'photoswipe/dist/photoswipe';
import PhotoSwipeUIDefault from 'photoswipe/dist/photoswipe-ui-default';

let vm, gallery;
export function showPhoto(config) {
  vm = new (Vue.extend(PreviewImgs, {}))({
    data: () => (Object.assign({
      share: false,
      fullscreen: false,
      showArrow: false,
      index: 0,
    }, config)),
    mounted() {
      document.body.appendChild(this.$el);
      this.init();
    },
    beforeDestroy() {
      document.body.removeChild(this.$el);
    },
    methods: {
      init() {
        let pswpElement = document.querySelectorAll('.pswp')[0];
        let options = {
          index: this.index, // 
          escKey: true, // esc
          bgOpacity: 0.9, // 
          allowPanToNext: true, // /
          loop: true, // 
          pinchToClose: false, // 
          closeOnScroll: true, // 
          closeOnVerticalDrag: true, // 
          history: false, // url
          fullscreenEl: this.fullscreen, // 
          shareEl: this.share, // 
          arrowEl: this.showArrow, // 
          tapToToggleControls: true, // 
          clickToCloseNonZoomable: true, // 
          // showHideOpacity: true, // opacity
          // 
          // getThumbBoundsFn: (index) => {
          //   let thumbnail = this.images[index].el;
          //   let pageYScroll = window.pageYOffset || document.documentElement.scrollTop;
          //   let rect = thumbnail.getBoundingClientRect();
          //   return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };
          // },
        };

        gallery = new PhotoSwipe(pswpElement, PhotoSwipeUIDefault, this.images, options);
        gallery.init();
        // gallery.listen('resize', function() {
        //   gallery.updateSize(true);
        // });
        // gallery.listen('close', function() {
        // });
      },
    },
  });
  vm.$mount();
}

export default {
  install(vue) {
    vue.prototype.$PreviewImg = showPhoto;
    vue.mixin({
      beforeRouteLeave(to, from, next) {
        gallery && gallery.close();
        next();
      },
    });
  },
};



